@import "~react-image-gallery/styles/css/image-gallery.css";

.favorite-movie-item-enter {
  opacity: 0;
  transform: translateY(-10px);
}
.favorite-movie-item-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: 500ms ease-in !important;
}
.favorite-movie-item-exit {
  opacity: 1;
}
.favorite-movie-item-exit-active {
  opacity: 0;
  transition: 500ms ease-in !important;
}

.lds-ring {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid #3F7FBF;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #3F7FBF transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}